body {
  background-color: #faf8ef;
  display: flex;
  justify-content: center;
  font-family: "Clear Sans", "Helvetica Neue" , 'sans-serif';
}

h1 {
  font-size: 80px;
  line-height: 0.7;
  color: #776E65;
  margin: 0px;
}

.container {
  width: 468px;
  margin-top: 10px;
}

.info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  width: 456px;
  height: 456px;
  background-color: #BBADA0;
  border: 7px solid #BBADA0;
  border-radius: 6px;
  margin-top: 20px;
}

.grid div {
  width: 100px;
  height: 100px;
  margin: 7px;
  border-radius: 3px;
  background-color: #EEE4DA;
  color: #afa192;
  font-weight: bold;
  text-align: center;
  font-size: 60px;
  line-height: 1.6;
}

.score-container {
  text-align: center;
  width: 70px;
  height: 60px;
  border-radius: 3px;
  background-color: #8f7a66;
  color: #FFFFFF;
}

#score {
  font-size: 30px;
}

.score-title {
  font-size: 16px;
}

.result {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
  border: 2px solid #333;
  border-radius: 5px;
  background-color: yellow;
  color: #333;
  width: 465px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.result.win {
  color: #28a745;
}

.result.lose {
  color: #dc3545;
}

.new-game{
  display: inline-block;
  margin-top: 16px;
    background: #8f7a66;
    border-radius: 3px;
    padding: 0 20px;
    text-decoration: none;
    color: #f9f6f2;
    height: 40px;
    cursor: pointer;
    display: block;
    text-align: center;
    flex-shrink: 0;
}

.container_new-game{
  display:flex;
  justify-content: center;
  align-items: center;
}

.grid div {
  transition: background-color 0.1s ease-in-out
}

.grid div:empty {
  display: none;
}
